@import url(https://fonts.googleapis.com/css?family=Noto+Sans+JP:100,400&subset=japanese);
@import url(https://fonts.googleapis.com/css?family=Noto+Serif:400,700,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Serif+JP:400,700,900&display=swap);
.postcontext{
  font-size: 1.2em;
}

.postcontext header{
  margin-bottom: 2em;
}

.postcontext h1{
  font-weight: 400;
  font-size: 2.7em;
  line-height: 1.2em;
  margin-bottom: 0.1em;
}

.postcontext .date, .postcontext .tags{
  display: inline-block;
  margin: 0;
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 100;
  font-size: 0.95em;
  color: #555;
}
.postcontext .date, .postcontext .tags span{
  margin: 0 0.4em;
}
.postcontext .date{
  margin-left: 0;
  font-weight: 700;
}
.postcontext .tags a{
  font-size: 1em;
}
.postcontext .tags a:hover{
  text-decoration: underline;
}
article{
  font-size: 1em;  
  line-height: 1.7em;
}
article p:first-child{
  display: none;
}
article h3{
  margin: 0.5em 0;
  font-size: 1em;
}
article a{
  color: #0d69da;
  text-decoration: none;
}
article .imgWrapper{
  margin: 0 auto;
  width: 55vw;
  text-align: center;
}
article a:hover{
  text-decoration: underline;
}
article code{
  padding: 0 0.2em;
  line-height: 0.5em;
  font-family: 'Courier New', Courier, monospace;
  font-size: 0.9em;
  color: crimson;
  background-color: #ddd;
  border: 1px solid #ccc;
  border-radius: 0.2em;  
}
article pre {
  padding: 0 0.2em;
  background-color: #ddd;
  border: 1px solid #ccc;
  border-radius: 0.2em;  
}
article pre code{
  border: none;
}
article hr{
  margin: 2em auto;
  width: 20%;
}
blockquote p:first-child{
  display: inline;
}
blockquote p{
  font-style: italic;
  color: #555;
}
article iframe{
  outline: none;
  border: none;
  width: 100%;
  height: 100%;
}
.youtube{
  margin: 0 auto;
  width: 55vw; 
  height: 30.1vw;
}
.postcontext nav{
  display: block;
  margin: 3em 0 1em 0;
  font-family: 'Courier New', Courier, monospace;
  font-size: 1.2em;
}
.postcontext nav a, .postcontext nav a:visited{
  color: #000;
  text-decoration: none;
}
.postcontext nav a:hover{
  text-decoration: underline;
}
.postcontext nav span{
  margin: 0 0.5em;
}
.postcontext nav span:first-child{
  margin-left: 0;
}
html, body{
  margin: 0;
  height: 100%;
  background-color: #eee;
  font-family: 'Noto Serif JP', serif;
}
#root{
  height: 100%;
}
.app{
  display: flex;
  flex-direction: column;
  padding: 0 20vw;
  height: 100%;  
}

header{
  margin: 2.5em 0 1em 0;
}

header a{
  color: #000;
  text-decoration: none;
}
header a:visited{
  color: #000;
}
header h1{
  margin: 0 0 0.5em 0;
  font-size: 3em;
  font-weight: 900;
  line-height: 1em;
}

.categoryFilter .category{
  display: inline-block;
  margin: 0;  
  padding: 0 0.5em;
  font-family: 'Courier New', Courier, monospace;
  font-weight: 100;
  border-left: solid 1px #000;
  color: #777;
}
.categoryFilter a:visited{
  color: #777;
}
.categoryFilter a:first-child h3{
  border: none;
  padding-left: 0;
}
.categoryFilter .current{
  color: #000;
  text-decoration: underline;
}
.postlist{
  flex: 1 0 auto;
}
.postlist .tagFilterResult{
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 1.7em;
  color: #555;
}
.postlink{
  margin: 1.5em 0;
}
.postlink h2{
  display: inline-block;
  margin: 0;
  font-weight: 400;
  font-size: 2.1em;
  color: #000;
  cursor: pointer;
}
.postlink .postmeta{
  /* display: inline-block; */
}
.postlink .date, .postlink .tags span{
  display: inline-block;
  margin: 0 0.6em;
  font-size: 1.1em;
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 100;
  color: #555;
}
.postlink .date{
  margin-left: 0;
  font-weight: 400;
}
.postlink .tags{
  display: inline-block;
}
.postlink .tags span:first-child{
  margin-left: 0;
}
.postlink .tags span:hover{
  text-decoration: underline;
}

footer{
  flex-shrink: 0;
  margin: 3em 0;
  font-family: 'Courier New', Courier, monospace;  
}
footer a, footer a:visited{
  color: #000;
  text-decoration: none;
}
footer a:hover{
  text-decoration: underline;
}

@media all and (max-width: 1200px){
  .app{
    font-size: 13px;
    padding: 0 10.5vw;
  }
}

@media all and (max-width: 768px){
  .app{
    font-size: 12px;
    padding: 0 10vw;
  }
}

@media all and (max-width: 420px){
  .app{
    font-size: 10px;
    padding: 0 5vw;
  }
}

